.inputIconWrapper {
  position: relative;
  width: 100%;
}
.inputIconWrapper .clearIcon {
  position: absolute;
  right: 5px;
  top: 6px;
  cursor: pointer;
  transform: rotate(45deg);
  width: 16px;
  height: 16px;
  padding: 7px 0;
  box-sizing: border-box;
}
.inputIconWrapper .clearIcon:before {
  transform: rotate(90deg);
}
.inputIconWrapper .clearIcon:before,
.inputIconWrapper .clearIcon:after {
  content: '';
  display: inline-block;
  height: 0;
  width: 100%;
  position: absolute;
  box-sizing: border-box;
  border: 1px solid gray;
  border-radius: 2px;
}
.inputWrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
}
.inputWrapper.labelInline {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.inputWrapper.labelInline label {
  margin-bottom: 0;
}
.inputWrapper.labelInline input {
  width: 50px;
}
.inputWrapper.error input {
  background: var(--c-error-bg, #ffdee2);
  border-color: var(--c-error-text, #ff8997);
}
.inputWrapper label {
  flex: 1;
  margin-bottom: 0.3em;
}
.inputWrapper input {
  padding: 5px;
  flex: 2;
  border: 1px solid #abb6bb;
  border-radius: 5px;
  box-sizing: border-box;
  vertical-align: top;
  background: var(--c-main-bg, #fff);
  outline: none;
}
.inputWrapper input:not(:disabled, :read-only):hover {
  border-color: #96a7b0;
  box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.2);
}
.inputWrapper input:focus {
  border-color: #4b85a0;
  box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.2);
}
.inputWrapper input:disabled {
  background: #eee;
  box-shadow: none;
}
.inputWrapper input:read-only {
  border-color: transparent;
  box-shadow: none;
}
