.agentStateText,
.agentNextStateText {
  row-gap: 5px;
  font-size: 11px;
  line-height: 13px;
  padding-left: 4px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
@media device-pixel-ratio {
  .agentStateText,
  .agentNextStateText {
    overflow: hidden;
    white-space: nowrap;
  }
}
.agentNextStateText {
  margin-top: 2px;
  color: var(--c-primary, #4183c4);
}
.agentStatusIcon {
  --agent-status-opacity: 0;
}
.agentStatusIcon circle {
  transition: opacity 0.3s ease;
}
.agentStatusIcon[data-active='true'] {
  --agent-status-opacity: 0.3;
}
