.avatar {
  position: relative;
  width: 1em;
  height: 1em;
  flex-shrink: 0;
  user-select: none;
}
.avatarHidden {
  visibility: hidden;
  opacity: 0;
}
.avatarVisible {
  width: 100%;
  height: 100%;
}
.avatarImage {
  display: block;
  font-size: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 50%;
}
.avatarImage.squareAvatar {
  border-radius: 5px;
}
.avatarInitials {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  font-size: 0.4em;
  line-height: 2em;
  text-align: center;
  color: var(--c-main-text-inverted, #fff);
  user-select: none;
  display: flex;
  justify-content: center;
  align-items: center;
}
.avatarInitials.squareAvatar {
  border-radius: 5px;
}
.user {
  background: var(--c-instance-user-bg, #64afd7);
}
.contact {
  background: var(--c-instance-contact-bg, #66d198);
}
.conference {
  background: var(--c-instance-conference-bg, #64afd7);
}
.unknown {
  background: var(--c-instance-unknown-bg, #798f9a);
}
.msteams {
  background: var(--c-instance-msteams-bg, #7b83eb);
}
.msteams_channel {
  background: var(--c-instance-msteams-channel-bg, #d50ac6);
}
.team {
  background: var(--c-instance-team-bg, #4b85a0);
}
.team-chat {
  background: var(--c-instance-team-bg, #4b85a0);
}
.static-entry,
.scenario-entry,
.hunt-group {
  background: var(--c-instance-directory-entry-bg, #9640d0cc);
}
.avatarHeadPhones {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: -0.1em;
}
.avatarHeadPhones.directoryEntry:before,
.avatarHeadPhones.directoryEntry:after {
  border-color: var(--c-instance-directory-entry-bg, #9640d0cc);
}
.avatarHeadPhones:before,
.avatarHeadPhones:after {
  content: '';
  border-color: var(--c-primary, #66b1d9);
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  margin: -0.03em;
}
.avatarHeadPhones:before {
  height: 50%;
  border-top-left-radius: 1em;
  border-top-right-radius: 1em;
  border-style: solid;
  border-width: 0.05em;
  border-bottom: 0;
}
.avatarHeadPhones:after {
  bottom: 0;
  height: 24%;
  margin: auto -0.05em;
  border-style: solid;
  border-width: 0 0.1em;
}
.avatarOnline {
  position: absolute;
  bottom: 0;
  right: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 0.5em;
  height: 0.5em;
  border-radius: 50%;
  overflow: hidden;
  margin: 0 -0.1em;
  background: var(--c-primary, #66b1d9);
  color: var(--c-main-text-inverted, #fff);
}
.avatarOnline .statusIcon {
  flex-grow: 1;
  width: 100% !important;
  height: 100% !important;
}
.avatarOnline :global .icon {
  width: 1em;
  margin: 0;
}
.avatarOnline :global .icon:before {
  vertical-align: middle;
}
.avatarLoader {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  margin-right: 0.25em;
  font-size: 0.37em !important;
  z-index: 0 !important;
}
.mediaTypeIcon {
  position: absolute;
  bottom: -2px;
  right: -5px;
  width: 55%;
  height: 55%;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  background: var(--c-primary, #2185d0);
  color: var(--c-main-text-inverted, #fff);
  font-size: 0.4em;
}
.mediaTypeIcon.unread {
  background: var(--c-error-border, #dc3b30);
}
.mediaTypeIcon img {
  width: 50%;
}
.avatarMsTeamsMark {
  position: absolute !important;
  width: 16px;
  height: 17px;
  margin-top: -4px;
  margin-left: -4px;
}
.smallerWithHeadphones {
  transform: scale(0.833);
}
.teamChatIcon {
  position: absolute;
  top: 0;
  right: 0;
  transform: translateX(50%);
}
