.slider {
  position: relative;
  padding: 6px;
  overflow: hidden;
}
.slider:hover .sliderRail,
.slider:hover .sliderRailColored,
.slider:hover .sliderHandle {
  filter: brightness(0.93);
}
.slider.disabled {
  pointer-events: none;
}
:global(.tab-highlighted) .slider:focus-within:not(.disabled) {
  outline: 2px solid var(--c-link, #538ec0);
}
.sliderPath {
  position: relative;
  height: 4px;
  width: 100%;
  cursor: pointer;
}
.sliderPathVertical {
  position: relative;
  width: 4px;
  height: 100%;
  cursor: pointer;
}
.sliderRail {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: 0 -6px;
  border-radius: 4px;
  background: var(--c-slider-base-color, #d8d8d8);
  transition: background 0.2s ease-in-out;
}
.sliderRailVertical {
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 4px;
  height: 100%;
  transform: translateX(-50%);
  border-radius: 4px;
  background: var(--c-slider-base-color, #d8d8d8);
}
.sliderRailColored {
  background: var(--c-slider-active-color, #1473e6);
}
.sliderRailColored:not(.sliderRailVertical) {
  border-bottom-right-radius: 0;
  border-top-right-radius: 0;
}
.slider.disabled .sliderRailColored {
  background: var(--c-slider-disabled-color, #d8d8d8);
}
.sliderHandle {
  position: absolute;
  top: 50%;
  left: 0;
  width: 12px;
  height: 12px;
  margin: -6px;
  border-radius: 50%;
  background: var(--c-slider-active-color, #1473e6);
  transition: background 0.2s ease-in-out;
}
.slider.disabled .sliderHandle {
  background: var(--c-slider-disabled-color #d8d8d8);
}
.sliderHandleVertical {
  position: absolute;
  left: 50%;
  transform: translate(-50%, 50%);
  bottom: 0;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background: var(--c-slider-active-color, #1473e6);
}
.slider.disabled .sliderHandleVertical {
  background: var(--c-slider-disabled-color #d8d8d8);
}
.sliderInput {
  position: absolute;
  left: 0;
  bottom: 100%;
}
.sliderInput:focus-visible {
  outline: none !important;
}
