.unread {
  background: #fbbd08 0 0 no-repeat border-box;
  color: #fff;
  width: 20px;
  height: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  font-size: 12px;
  line-height: 1;
  font-weight: 400;
  flex-shrink: 0;
}
.unread.inline {
  display: inline-flex;
}
.unread.muted {
  border: 1px solid var(--c-icon, #767676);
  background: #fbbd0800;
  color: var(--c-icon, #767676);
}
