:global(.ui.dropdown).dropdown:global(.fluid) {
  display: flex !important;
}
:global(.ui.dropdown).dropdown:global(.fluid.inline) {
  display: inline-flex !important;
}
:global(.ui.dropdown).dropdown:global(.active.selection).openTop {
  border-top: 1px solid #fafafa;
  border-top-right-radius: 0;
  border-top-left-radius: 0;
}
:global(.ui.dropdown).dropdown:global(.active.selection).openBottom {
  border-bottom: none;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
}
:global(.ui.dropdown).dropdown:global(.fluid > .text) {
  width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
:global(.ui.dropdown).dropdown:global(.search) > :global(.text) {
  z-index: 1;
}
:global(.ui.dropdown).dropdown:global(.search) > input:global(.search) {
  width: calc(100% - 1px);
}
:global(.ui.dropdown).dropdown > :global(.dropdown.icon) {
  float: none !important;
  align-self: center;
}
:global(.ui.dropdown).menu {
  pointer-events: auto;
}
:global(.ui.dropdown).menu:global(.dropdown.active) {
  z-index: 1001;
}
:global(.ui.dropdown).menu > :global(.menu) {
  display: block;
  position: relative;
}
:global(.ui.dropdown).menu[data-popper-reference-hidden='true'] {
  visibility: hidden;
  pointer-events: none;
  opacity: 0;
}
:global(.ui.dropdown).menu.selection {
  padding: 0;
  min-height: 0;
}
:global(.ui.dropdown).menuTop:global(.active.selection),
:global(.ui.dropdown).menuTop:global(.active.selection:hover) {
  border-bottom: none;
  box-shadow: none;
}
:global(.ui.dropdown).menuTop:global(.active.selection) > :global(.menu),
:global(.ui.dropdown).menuTop:global(.active.selection:hover) > :global(.menu) {
  border-bottom: 0;
  box-shadow: none;
}
:global(.ui.dropdown).menuTop:global(.active) > :global(.menu),
:global(.ui.dropdown).menuTop:global(.active:hover) > :global(.menu) {
  border-radius: 0.2857rem 0.2857rem 0 0;
}
:global(.ui.dropdown).menuBottom:global(.active),
:global(.ui.dropdown).menuBottom:global(.active.selection) {
  border-top: none;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}
