:root {
  --floating-panel-border-width: 8px;
}
.floatingEar {
  top: 9px;
  background: var(--c-main-bg);
  position: absolute;
  z-index: -1;
  box-shadow: 0 0 4px #00000047;
}
.floatingEar.pinned {
  box-shadow: none;
}
.floatingEar.closed {
  box-shadow: none;
}
.floating.closed {
  width: 0;
}
.floatingContainer {
  z-index: 100;
  height: 100%;
  background: var(--c-main-bg);
  overflow: visible;
  transition: all 0.3s ease-in-out;
  position: absolute;
  box-shadow: 0 4px 6px #00000047;
  display: flex;
  flex-direction: column;
}
.floatingContainer.pinned {
  position: relative;
  box-shadow: none;
}
.floatingContainer.closed {
  box-shadow: none;
}
.floatingContainer.right {
  right: 0;
  border-left: 1px solid var(--c-main-border, #e2e2e3);
}
.floatingContainer.right.closed {
  transform: translateX(100%);
}
.floatingContainer.right .floatingEar {
  left: 0;
  transform: translateX(-100%);
}
.floatingContainer.right.pinned {
  border-left: var(--floating-panel-border-width) solid var(--c-legacy-main-bars-bg, #526070);
}
.floatingContainer.left {
  left: 0;
  border-right: 1px solid var(--c-main-border, #e2e2e3);
}
.floatingContainer.left.closed {
  transform: translateX(-100%);
}
.floatingContainer.left .floatingEar {
  right: 0;
  transform: translateX(100%);
}
.floatingContainer.left.pinned {
  border-right: var(--floating-panel-border-width) solid var(--c-legacy-main-bars-bg, #526070);
}
.draggable {
  z-index: calc(100 + 1);
}
.draggable.pinned.right {
  transform: translateX(calc(-100% + var(--floating-panel-border-width)));
  background: transparent;
}
.draggable.pinned.left {
  transform: translateX(calc(100% + -1 * var(--floating-panel-border-width)));
  background: transparent;
}
.ear {
  cursor: pointer;
  width: 33px;
  height: 32px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #e2e2e3;
}
.earIcon {
  display: inline-block;
}
.ear.left {
  border-radius: 5px 0 0 5px;
  border-right: none;
}
.ear.right {
  border-radius: 0 5px 5px 0;
  border-left: none;
}
