.window {
  width: 100%;
  border-radius: 4px;
  box-shadow: 0 3px 6px #00000029;
  border: 1px solid #dadadb;
  background-color: white;
  display: flex;
  flex-direction: column;
  transition: 0.3s ease all;
  flex: 1 1 auto;
}
.window.minimized {
  transform: translateY(calc(100% - 60px));
}
.header {
  display: flex;
  align-items: center;
  height: 60px;
  background-color: #f3f3f3;
  padding: 10px 8px 10px 16px;
  justify-content: space-between;
}
.headerContent {
  display: flex;
  align-items: center;
}
.title {
  font-weight: bold;
}
.controls {
  display: flex;
  align-items: center;
}
.control svg path {
  transition: all 0.3s ease;
}
.control svg path:global(.fill) {
  stroke: #333;
  stroke-width: 0;
}
.control:hover svg path:global(.svg-stroke) {
  stroke: #333;
  stroke-width: 1.5px;
}
.control:hover svg path:global(.fill) {
  fill: #333;
  stroke-width: 1px;
}
.row {
  display: flex;
  align-items: center;
}
.content {
  padding: 0 8px 8px;
  display: flex;
  flex: 1 1 auto;
  max-height: 100%;
}
.content.minimized {
  max-height: 0;
}
