.controlRemoveRow button,
.controlAddRow button {
  border: none;
  background: none;
}
.controlRemoveRow button:not(:disabled),
.controlAddRow button:not(:disabled) {
  cursor: pointer;
}
.controlRemoveRow {
  display: flex;
  align-items: center;
}
.controlAddRow button {
  padding-left: 0;
}
.controlAddRow span {
  margin-left: 0.3em;
  vertical-align: middle;
  color: var(--c-main-text-secondary, rgba(51, 51, 51, 0.87));
}
.fieldArrayRowLabel {
  margin-bottom: 0 !important;
}
.fieldArrayRow:not(:first-child) {
  margin-top: 24px;
}
:global(.ui.form textarea:not([rows])).textArea {
  min-height: auto;
  max-height: 100%;
}
:global(.ui.form .field).datePicker {
  margin-bottom: 0;
}
.messengerButton {
  border: none;
  background: none;
  padding: 0;
  cursor: pointer;
  display: flex;
}
