.countrySelectorButton {
  height: 38px;
  width: 38px;
  padding: 7px !important;
  display: flex !important;
  align-items: center;
  justify-content: center;
}
.countrySelectorButtonIcon {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 20px;
  width: 20px;
}
.countrySelectorButtonFlag {
  margin: 0 !important;
}
.countrySelectorContainer {
  margin-top: 1px;
  display: flex;
  flex-flow: column nowrap;
  height: 270px;
  width: 250px;
  border: 1px solid var(--c-main-border, #dededf);
  border-radius: 4px;
  box-shadow: 0 3px 6px #00000029;
  background-color: #fff;
  color: #000;
}
.countrySelectorSearchInput {
  margin: 12px 10px 0 6.5px;
}
.countrySelectorList {
  overflow: auto;
  margin-top: 6px !important;
}
.countrySelectorAllCountriesItem {
  display: flex;
}
.countrySelectorAllCountriesItem > .countrySelectorFlag {
  margin-right: 6px;
  display: flex;
  align-items: center;
}
.countrySelectorHeader {
  font-weight: 600;
  margin: 2px 0;
}
.countrySelectorItem {
  padding: 0 !important;
  height: 28px;
  line-height: 28px !important;
  padding-left: 15.5px !important;
}
.countrySelectorItem:not(.countrySelectorHeader) {
  cursor: pointer;
}
.countrySelectorItem:hover:not(.countrySelectorHeader),
.countrySelectorItemSelected {
  background: var(--c-contact-list-bg-hover, #f3f3f3);
}
.countrySelectorCountryItem {
  display: flex;
  align-items: center;
}
.countrySelectorCountryItemName {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  margin-right: 6px;
}
.countrySelectorPhonePrefix {
  margin: 0 7px 0 auto;
}
